.App {
  text-align: center;
}

.drunk1-logo {
  height: 60vmin;
  pointer-events: none;
  position: absolute;
  top: 110px;
  left: 200px;
}

.drunk2-logo {
    height: 30vmin;
    pointer-events: none;
    position: absolute;
    top: 500px;
    right: 60px;
}

.mari1-logo {
    height: 30vmin;
    pointer-events: none;
    position: absolute;
    top: 500px;
    left: 30px;
}

.mari2-logo {
    height: 45vmin;
    pointer-events: none;
    position: absolute;
    top: 250px;
    right: 123px;
}

.mari3-logo {
    height: 15vmin;
    pointer-events: none;
    position: absolute;
    top: 60px;
    right: 30px;
}

.key-logo {
  height: 5vmin;
  pointer-events: none;
  position: absolute;
  top: 60px;
  left: 200px;
}

@media (prefers-reduced-motion: no-preference) {
  .drunk1-logo {
    animation: drunk1-logo-spin infinite 20s linear;
  }

  .drunk2-logo {
    animation: drunk2-logo-spin infinite 20s linear;
  }
   
  .mari1-logo {
    animation: mari1-logo-spin infinite 30s linear;
  }
  .mari2-logo {
    animation: mari2-logo-spin infinite 45s linear;
  }
  .mari3-logo {
    animation: mari3-logo-spin infinite 10s linear;
  }
  .key-logo {
    animation: key-logo-spin infinite 35s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes drunk1-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes drunk2-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes mari1-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes mari2-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes mari3-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes key-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
